<template>
  <div class="skills">
    <b-form-radio-group label="기술" v-slot="{ ariaDescribedby }" @change="skillData">
        <div :key="index" v-for="(skill, index) in $store.state.skill">
          <b-form-radio :id="skill.htmlClass" :aria-describedby="ariaDescribedby" name="skills" :value="index">{{skill.name}}</b-form-radio>
        </div>
    </b-form-radio-group>
    </div>
</template>

<script>
export default {
  methods: {
    skillData(value) {
      return this.$store.commit("updateMySelectedSkill", value);
    }
  }
}
</script>

<style scoped>
.skills {
    display: flex;
    flex-direction: column;
}

</style>