<template>
  <div id="down">
    <transition name="buffs">
      <div class="buffs" v-if="wrap && title === '내 포켓몬'">
        <b-form-group label="캐릭터 버프/디버프:" v-slot="{ ariaDescribedby }">
          <b-form-checkbox-group id="checkbox-group-2"
            :aria-describedby="ariaDescribedby"
            name="buffs"
            @change="changeBuff"
          >
            <b-form-checkbox :key="index" v-for="(buffs, index) in $store.state.buffs" :value="index">{{buffs.name}}</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
        <b-form-group label="변수:" v-slot="{ ariaDescribedby }">
          <b-form-checkbox-group id="checkbox-group-2"
            :aria-describedby="ariaDescribedby"
            name="variable"
            @change="addBuffs"
          >
            <b-form-checkbox value="급소">급소</b-form-checkbox>
            <b-form-checkbox value="초점렌즈">초점렌즈</b-form-checkbox>
            <b-form-checkbox value="박식안경">박식안경</b-form-checkbox>
            <b-form-checkbox value="에너지증폭기">에너지증폭기</b-form-checkbox>
            <b-form-checkbox value="플러스파워">플러스파워</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
        <MyItems :title="title"></MyItems>
      </div>
      <div class="buffs" v-if="wrap && title !== '내 포켓몬'">
        <b-form-group label="캐릭터 버프/디버프:" v-slot="{ ariaDescribedby }">
          <b-form-checkbox-group id="checkbox-group-2"
            :aria-describedby="ariaDescribedby"
            name="debuffs"
            @change="changeDebuff"
          >
            <b-form-checkbox :key="index" v-for="(debuffs, index) in $store.state.debuffs" :value="index">{{debuffs.name}}</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
        <!-- <Items :title="title"></Items> -->
      </div>
    </transition>
      <b-button class="wrap" variant="info" @click="turnWrap" v-if="!wrap">고급설정</b-button>
      <b-button class="wrap" variant="info" @click="turnWrap" v-if="wrap">숨기기</b-button>
  </div>
</template>

<script>
import MyItems from './MyItems.vue'

export default {
  components: {
    MyItems
  },
  data() {
    return {
      wrap: false
    }
  },
  methods: {
    turnWrap() {
      this.wrap = !this.wrap;
    },
    addBuffs(value) {
      let returnValue = []
      if (value.includes('급소')) {
        returnValue.push({
          "name": "급소",
          "before": false,
          "myself": true,
          "skill": -1,
          "canStack": 1,
          "affect": {
            "skillRatio": 2,
            "hp": 1,
            "attack": 1,
            "defense": 1,
            "sp_attack": 1,
            "sp_defense": 1
          }
        })
        if (value.includes('초점렌즈')) {
          returnValue.push({
            "name": "초점렌즈",
            "before": false,
            "myself": true,
            "skill": -1,
            "canStack": 1,
            "affect": {
              "skillRatio": 1.06,
              "hp": 1,
              "attack": 1,
              "defense": 1,
              "sp_attack": 1,
              "sp_defense": 1
            }
          })
        }
      } 
      if (value.includes('박식안경')) {
        returnValue.push({
          "name": "박식안경",
          "before": true,
          "myself": true,
          "skill": -1,
          "canStack": 1,
          "affect": {
            "skillRatio": 1,
            "hp": 1,
            "attack": 1,
            "defense": 1,
            "sp_attack": 1.07,
            "sp_defense": 1
          }
        })
      }
      if (value.includes('에너지증폭기')) {
        returnValue.push({
          "name": "에너지증폭기",
          "before": false,
          "myself": true,
          "skill": -1,
          "canStack": 1,
          "affect": {
            "skillRatio": 1.21,
            "hp": 1,
            "attack": 1,
            "defense": 1,
            "sp_attack": 1,
            "sp_defense": 1
          }
        })
      }
      if (value.includes('플러스파워')) {
        returnValue.push({
          "name": "플러스파워",
          "before": true,
          "myself": true,
          "skill": -1,
          "canStack": 1,
          "affect": {
            "skillRatio": 1,
            "hp": 1,
            "attack": 1.2,
            "defense": 1,
            "sp_attack": 1.2,
            "sp_defense": 1
          }
        })
      }
      this.$store.commit("addBuffs", returnValue);
    },
    changeBuff(value) {
        this.$store.commit("changeBuffs", value);
      },
    changeDebuff(value) {
        this.$store.commit("changeDebuffs", value);
      }
  },
  props: ['title']
}
</script>

<style scoped>
#down {
  width: 100%;
  padding: 1rem;
  margin: 0;

  border-top: 1px solid gray;
}

.wrap {
  display: block;
  font-size: 0.8rem;
  margin: 0.3rem auto;
}

.buffs-enter,
.buffs-leave-to {
  visibility: hidden;
  height: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
}

.buffs-enter-active,
.buffs-leave-active {
  transition: all 0.5s;
}
</style>