export const mutations = {
    //initial
    updateInitial() {
        let idx = 0;
        this.dispatch("GET_PKINFO", [idx, true]);
    },
    updatePokemonList(state, data) {
        state.PokemonList = data.list
    },
    updateItemList(state, data) {
        state.itemList = data.list
    },
    updateItemInfo(state, data) {
        state.itemInfo = data
    },

    //Update Mine
    updateMySelectedPokemon(state, value) {
        state.selected.mypokemon = value;
        let idx = state.PokemonList.indexOf(value);
        this.dispatch("GET_PKINFO", [idx, true]);
    },
    updatePokemonInfo(state, payload) {
        if (payload[1]) {
            state.canCalculate = payload[0].canCalculate;
            state.skill = payload[0].skill;
            state.buffs = payload[0].buffs;
            state.selected.mystats = payload[0].stats;
        } else {
            state.selected.oppostats = payload[0].stats;
            state.debuffs = payload[0].debuffs;
        }
        this.dispatch("CAL_DAMAGE");
    },
    updateMySelectedSkill(state, value) {
        state.selected.myskill = value;
        this.dispatch("CAL_DAMAGE");
    },
    updateMySelectedLevel(state, value) {
        state.selected.mylevel = value;
        this.dispatch("CAL_DAMAGE");
    },

    updateOppoSelectedLevel(state, value) {
        state.selected.oppolevel = value;
        this.dispatch("CAL_DAMAGE");
    },
    updateOppoSelectedPokemon(state, value) {
        state.selected.oppopokemon = value;
        let idx = state.PokemonList.indexOf(value);
        this.dispatch("GET_PKINFO", [idx, false]);
    },

    // Calculate Max Dmg
    setDmg(state, value) {
        state.damage = value;
        state.maxdamage = 0;
        ("state.damage", state.damage);
        for (let dmg of state.damage) {
            state.maxdamage += dmg.damage * dmg.tick;
        }
    },
    
    //Buffs
    changeBuffs(state, value) {
        state.selected.mybuffs = [];
        for (let i of value) {
            state.selected.mybuffs.push(state.buffs[i]);
        }
        this.dispatch("CAL_DAMAGE");
    },
    changeDebuffs(state, value) {
        state.selected.oppobuffs = [];
        for (let i of value) {
            state.selected.oppobuffs.push(state.debuffs[i]);
        }
        this.dispatch("CAL_DAMAGE");
    },
    addBuffs(state, value) {
        for (let i = 0; i < state.selected.mybuffs.length; i++) {
            if (state.selected.mybuffs[i].name === '급소' || state.selected.mybuffs[i].name === '박식안경' || state.selected.mybuffs[i].name === '에너지증폭기' || state.selected.mybuffs[i].name === '초점렌즈' || state.selected.mybuffs[i].name === '플러스파워') {
                state.selected.mybuffs.splice(i, 1);
                i -= 1
            }
        }
        for (let i of value) {
            state.selected.mybuffs.push(i)
        }
        this.dispatch("CAL_DAMAGE");
    },
    removeBuffs(state) {
        state.selected.mybuffs = [];
        this.dispatch("CAL_DAMAGE");
    },

    //StatBoost
    addMyStatBoost(state, value) {
        state.selected.mystatboost[value[0]] = value[1];
        this.dispatch("CAL_DAMAGE");
    },
    resetMyStatBoost(state) {
        state.selected.mystatboost = {
            'hp': 0,
            'attack': 0,
            'defense': 0,
            'sp_attack': 0,
            'sp_defense': 0,
        };
        this.dispatch("CAL_DAMAGE");
    },
    addMyItem(state, value) {
        let selectedItem = state.itemInfo[value]
        for (let stat of selectedItem.stats) {
            state.selected.mystatboost[stat.name] += stat.amount;
        }
        this.dispatch("CAL_DAMAGE");
    },

    //nullify
    nullifyOpponent(state) {
        state.selected.oppopokemon = '대타출동인형'
        state.selected.oppolevel = 1
        state.selected.oppobuffs = [],
            state.selected.oppostatboost = {
                'hp': 0,
                'attack': 0,
                'defense': 0,
                'sp_attack': 0,
                'sp_defense': 0,
            };
        state.selected.oppostats = [{
            hp: 10000
        }]
        this.dispatch("CAL_DAMAGE");
    }
}