import axios from 'axios'

const host = "https://iant.kr"

export const GetPokemonList = () => {
    axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    return axios.get(`${host}:5500/pokemonList`)
}

export const GetPokemonInfo = (index) => {
    axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    return axios.get(`${host}:5500/pokemon/${index}`)
}

export const GetItemList = () => {
    axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    return axios.get(`${host}:5500/itemList`)
}

export const GetItemDetails = () => {
    axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    return axios.get(`${host}:5500/items`)
}