import Vue from 'vue'
import VueRouter from 'vue-router'
import Simple from '../views/Simple'
import More from '../views/More'


Vue.use(VueRouter);

export const router = new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/unite-dmg',
            name: 'simple',
            component: Simple
        },
        {
            path: '/unite-dmg/more',
            name: 'more',
            component: More
        }
    ]
})