<template>
  <div class="pokemon">
    <b-form-select v-model="pokemon" class="mt-3">
      <b-form-select-option v-bind:key="pkName" v-bind:id="`pokemon${index}`" v-for="(pkName, index) in this.$store.state.PokemonList" :value="`${pkName}`">{{pkName}}</b-form-select-option>
    </b-form-select>
  </div>
</template>

<script>
export default {
  computed: {
    pokemon: {
      get() {
        if (this.title == '내 포켓몬') {
          return this.$store.state.selected.mypokemon;
        } else {
          return this.$store.state.selected.oppopokemon;
        }
      },
      set(value) {
        if (this.title == '내 포켓몬') {
          this.$store.commit("updateMySelectedPokemon", value);
        } else {
          this.$store.commit("updateOppoSelectedPokemon", value);
        }
      }
    },
  },
  props: ['title']
}
</script>

