<template>
  <div class="pokemon">
    <Pokemon class="my-pokemon" :title="'내 포켓몬'"></Pokemon>
    <Result class="result"></Result>
</div>
</template>

<script>
import Pokemon from "../components/Pokemon.vue"
import Result from '../components/Result.vue'

export default {
  components: {
    Pokemon,
    Result
  },
  created() {
    this.$store.dispatch("GET_PKLIST");
    if (this.$store.state.selected.oppopokemon !== '대타출동인형') {
      this.$store.commit("nullifyOpponent")
    }
  },
}
</script>

<style scoped>
.my-pokemon {
  width: 90%;
  margin: 1rem auto;
}

.oppo-pokemon {
  width: 90%;
  margin: 1rem auto;
}

.result {
  width: 70%;
  margin: 1rem auto;
}
</style>