<template>
  <div id="app">
    <div class="stat-control">
      <b-form-group label="아이템 목록:">
        <span class="stats">
          <label for="hp">HP: </label>
          <b-form-input name="hp" id="stat-input" type="number" min="0" @change="changeHP" v-model="hp"></b-form-input>
        </span>
        <span class="stats">
          <label for="attack">공격: </label>
          <b-form-input name="attack" id="stat-input" type="number" min="0" @change="changeAttack" v-model="attack"></b-form-input>
        </span>
        <span class="stats">
          <label for="sp_attack">특수공격: </label>
          <b-form-input name="sp_attack" id="stat-input" type="number" min="0" @change="changeSpAttack" v-model="sp_attack"></b-form-input>
        </span>
      </b-form-group>
    </div>
    <b-button class="item-button" variant="danger" v-on:click="setItem($event)" value="reset">초기화</b-button>
    <b-button class="item-button" variant="success" v-on:click="setItem($event)" :key="item.label" v-for="item in $store.state.itemList" :value="item.label">{{item.name}}</b-button>
  </div>
</template>

<script>
export default {
  created() {
    this.hp = this.$store.state.selected.mystatboost.hp;
    this.attack = this.$store.state.selected.mystatboost.attack;
    this.sp_attack = this.$store.state.selected.mystatboost.sp_attack;
  },
  data() {
    return {
      attack: 0,
      hp: 0,
      sp_attack: 0,
    }
  },
  destroyed() {
    this.$store.commit('resetMyStatBoost');
    this.$store.commit("removeBuffs")
  },
  methods : {
    setItem(value) {
      let type = value.currentTarget.getAttribute('value')
      if (type === 'reset') {
        this.hp = 0;
        this.attack = 0;
        this.sp_attack = 0;
        this.$store.commit('resetMyStatBoost');
      } else {
        this.$store.commit('addMyItem', type);
        this.hp = this.$store.state.selected.mystatboost.hp;
        this.attack = this.$store.state.selected.mystatboost.attack;
        this.sp_attack = this.$store.state.selected.mystatboost.sp_attack;
      }
    },
    changeHP(value) {
      this.hp = Number(value)
      this.$store.commit('addMyStatBoost', ['hp', Number(value)]);
    },
    changeAttack(value) {
      this.attack = Number(value)
      this.$store.commit('addMyStatBoost', ['attack', Number(value)]);
    },
    changeSpAttack(value) {
      this.sp_attack = Number(value)
      this.$store.commit('addMyStatBoost', ['sp_attack', Number(value)]);
    }
  }
}
</script>

<style scoped>

#app {
  width: 100%;
}

.stat-control {
  width: 100%;
}

.stats {
  display: inline-block;
}

#stat-input {
  width: 5rem;
  display: inline-block;
  margin: 0.5rem;
}

.item-button {
  margin: 0.3rem;
  font-size: 0.9rem;
}

</style>